import {
  request
} from "./request"
import qs from "qs"

export function login(username, password) {
  return request({
    url: '/login',
    method: 'POST',
    data: qs.stringify({
      username,
      password
    })
  })
}

export function findUserByToken() {
    return request({
        url: '/findUserByToken',
        method: 'GET'
    })
}
export function testBar() {
  return request({
    url: '/bar',
    method: 'GET'
  })
}