import Vue from 'vue'
import VueRouter from 'vue-router'
import store from "../store"

import {
  findUserByToken
} from "network/login"
import {
  SET_USER
} from "store/mutation_types";

Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push
   VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

const routes = [{
    path: '',
    redirect: "/home"
  },
  {
    path: '/home',
    component: () => import("views/home/Home"),
    meta: {
      requireAuth: true
    },
    children: [
      {
        path: "",
        name: 'Home',
        component: () => import("views/house/HouseList"),
        meta: {
          title: "数据总览",
          subtitle: "租房数据",
          requireAuth: true
        }
      },
      {
        path: "proxy",
        name: 'Proxy',
        component: () => import("views/proxy/Proxy"),
        meta: {
          title: "代理池查看",
          subtitle: "代理池",
          requireAuth: true
        }
      },
      {
        path: "base",
        component: () => import("views/analysis/Base"),
        meta: {
          title: "租金、标签信息及租赁方式",
          subtitle: "可视化分析",
          requireAuth: true
        }
      },
      {
        path: "houseSource",
        component: () => import("views/analysis/HouseSource"),
        meta: {
          title: "区域分析",
          subtitle: "可视化分析",
          requireAuth: true
        }
      },
      {
        path: "cost",
        component: () => import("views/analysis/Cost"),
        meta: {
          title: "价格分析",
          subtitle: "可视化分析",
          requireAuth: true
        }
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import("views/login/Login"),
    meta: {
      title: "登录"
    }
  },
  {
    path: '/401',
    component: () => import('views/error/401'),
    meta: {
      title: '401'
    }
  },
  {
    path: '/500',
    component: () => import('views/error/500'),
    meta: {
      title: '500'
    }
  },
  {
    path: '*',
    component: () => import('views/error/404'),
    meta: {
      title: '404'
    }
  }
]

const router = new VueRouter({
  // mode: 'history',
  routes
})


router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  if (to.meta.requireAuth) {
    if (localStorage.token) {
      // 如果store中已有用户信息则放行
      if (store.getters.user.username) {
        next();
      }
      // 通过token(请求头中)获取用户信息
      findUserByToken().then(res => {
        if (res.data.statusCode == 200) {
          store.commit(SET_USER, {
            user: {
              id: res.data.data.userId,
              username: res.data.data.username
            }
          });
          next();
        } else {
          localStorage.removeItem("token")
          next({
            path: '/login'
          });
        }
      }).catch(err => {
        router.push("/500")
      })
    } else {
      next({
        path: '/login'
      });
    }
  } else {
    next();
  }
})



export default router